*,
:after,
:before {
  box-sizing: border-box; }

body,
html {
  margin: 0;
  padding: 0; }

body {
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  padding-bottom: 2rem; }

main {
  position: relative; }

.appNav ul, .searchApp, .resultsNav .result, .infoWindow .title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.appTitle {
  margin-top: 0;
  text-align: center; }

.appHeader,
.searchApp {
  width: 100%;
  padding: 30px 15px; }

.searchApp {
  padding: 0; }

.fieldDestination,
.appNav {
  width: 100%;
  margin: 15px auto; }
  @media (min-width: 1024px) {
    .fieldDestination,
    .appNav {
      width: 70%; } }
  @media (min-width: 1300px) {
    .fieldDestination,
    .appNav {
      width: 40%; } }

.fieldDestination input {
  width: 100%;
  padding: 15px 30px;
  font-size: 18px;
  border-radius: 5px;
  border: 1px solid #aaa; }

.appNav ul {
  list-style-type: none;
  padding: 0;
  margin: 0 -5px;
  justify-content: center; }
  .appNav ul li {
    padding: 5px 5px;
    flex-basis: 50%;
    max-width: 50%; }
    .appNav ul li:last-child {
      flex-basis: 100%;
      max-width: 100%; }
    @media (min-width: 640px) {
      .appNav ul li {
        flex-basis: 33.333%;
        max-width: 33.333%; }
        .appNav ul li:last-child {
          flex-basis: 33.333%;
          max-width: 33.333%; } }
    .appNav ul li button {
      display: block;
      width: 100%;
      padding: 7px 15px;
      cursor: pointer;
      background-color: #D6D6D6;
      border: none; }
      .appNav ul li button:focus {
        outline: none; }
      .appNav ul li button:hover {
        background-color: #c9c9c9; }
    .appNav ul li svg {
      max-height: 15px;
      margin-bottom: 5px;
      width: auto; }
    .appNav ul li.active button {
      color: #fff; }
    .appNav ul li.active svg path {
      fill: #fff; }
    .appNav ul li.active.hotels button {
      background-color: #0E0EA4; }
    .appNav ul li.active.attractions button {
      background-color: #0BC474; }
    .appNav ul li.active.bars button {
      background-color: #710E0E; }

.error-message {
  background-color: #f7baba;
  color: #710E0E;
  padding: 15px; }

.appNav ul, .searchApp, .resultsNav .result, .infoWindow .title {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

body.showMap .searchApp {
  opacity: 1; }

img {
  max-width: 100%;
  height: auto; }

.map {
  min-height: 70vh;
  position: relative; }

.searchApp {
  opacity: 0;
  transition: all 0.3s;
  margin: 0; }
  @media (min-width: 768px) {
    .searchApp {
      margin: 0 -15px; } }
  .searchApp .map {
    flex-basis: calc(100% - 30px);
    max-width: calc(100% - 30px);
    margin: 30px auto 0 auto;
    padding: 0 15px; }
    @media (min-width: 768px) {
      .searchApp .map {
        flex-basis: 65%;
        max-width: 65%;
        margin-top: 0; } }
    @media (min-width: 1024px) {
      .searchApp .map {
        flex-basis: 75%;
        max-width: 75%; } }
  .searchApp .results {
    flex-basis: 100%;
    max-width: 100%;
    padding: 0 15px;
    max-height: 70vh;
    overflow-y: scroll;
    text-align: center; }
    @media (min-width: 768px) {
      .searchApp .results {
        flex-basis: 35%;
        max-width: 35%; } }
    @media (min-width: 1024px) {
      .searchApp .results {
        flex-basis: 25%;
        max-width: 25%; } }

.resultsNav .noResult {
  width: 100%; }

.resultsNav .result {
  align-items: center;
  text-align: left;
  padding: 15px;
  cursor: pointer; }
  .resultsNav .result:nth-child(odd) {
    background-color: #d9d9d9; }
  .resultsNav .result.active {
    background-color: #f7baba;
    color: #710E0E; }
  .resultsNav .result .ico {
    flex-basis: 40px;
    max-width: 40px; }
  .resultsNav .result .resultContent {
    flex-basis: calc(100% - 40px);
    max-width: calc(100% - 40px);
    padding-left: 15px; }
    .resultsNav .result .resultContent .resultName {
      margin: 0;
      font-size: 1rem;
      font-weight: 600; }

button.more {
  padding: 10px 20px;
  background-color: #710E0E;
  color: #fff;
  border: none;
  cursor: pointer;
  margin-top: 10px; }
  button.more:disabled {
    opacity: 0.7;
    cursor: auto; }

.infoWindow .title {
  flex-basis: 100%;
  max-width: 100%;
  align-items: center; }
  .infoWindow .title img {
    width: 30px !important;
    height: 30px; }
  .infoWindow .title h6 {
    flex-basis: 70%;
    max-width: 70%;
    margin: 0;
    padding-left: 15px;
    font-size: 1rem; }

.infoWindow table {
  margin-top: 10px; }
  .infoWindow table td,
  .infoWindow table th {
    vertical-align: top;
    padding: 5px; }
  .infoWindow table th {
    text-align: center; }
  .infoWindow table a {
    color: #710E0E;
    font-weight: bold;
    text-decoration: none; }
    .infoWindow table a:hover {
      text-decoration: underline; }
